.cell {
  border: 1px solid transparent;
  width: 5px;
  height: 5px;
  background: rgba(200, 200, 200, 0.65);
  display: inline-block;
}

.cell.o {
  box-shadow: 1px 1px 25px 2px blue;
  background: blue;
}
.cellWrap{
  overflow: hidden;
  display: inline-table;
  border: 3px solid rgba(0, 0, 0, 0.6);
  border-radius: 5px;
}
.cell-row {
  padding: 0;
  margin: 0;
  display: flex;
}

button {
  border: 2px solid rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  box-shadow: none;
  min-width: 150px;
  padding: 5px 22px;
  margin: 10px;
}
button:hover {
  background: rgba(58, 96, 221, 0.07);
  transition: 100ms border ease-in;
  border: 2px solid rgba(0, 0, 0, 0.6);
}
